.place {

  &__photo {
    width: 100%;
    height: 100%;
    border-radius: 8px;
  }

  &__container {
    padding-bottom: 64px;

    @include onTablet {
      padding-bottom: 88px;
    }
  }

  &__description {
    &--cake {
      margin: 0;
      padding-top: 16px;
      font-weight: 500;

      letter-spacing: 0.02em;
      text-transform: uppercase;

      color: $colorGrey;
    }

    &--text {
      margin: 0;
      padding-top: 8px;
    }

    &--order {
      display: flex;
      margin: 0;
      padding-top: 24px;
      font-weight: 500;
      line-height: 100%;

      letter-spacing: 0.02em;
      text-transform: uppercase;

      color: $colorOrange;
    }
  }

  &__vector {
    height: 16px;
    width: 16px;
    padding-right: 8px;
  }

  &__img {
    box-sizing: border-box;
    height: 290px;
    width: 290px;
    background-size: contain;
    background-position: bottom;
    background-repeat: no-repeat;

    margin-top: -544px;
    margin-bottom: 220px;
    margin-left: auto;
    margin-right: auto;

    @include onTablet {
      display: block;
      margin-top: -610px;
      margin-bottom: 124px;
      margin-left: auto;
      margin-right: auto;

      height: 493px;
      width: 526px;
    }
  }
}
